.App {
  text-align: center;
}


/* ---------------여기부터 수업시간에 함-------------------- */
/*  2023/9/11 월요일 */

div{
  box-sizing: border-box;

}

.list{
  padding-left: 20px;
  text-align: left;
  border-bottom: 1px solid gray;
}



.black-nav{
  display: flex;  /*쫙 펼쳐지는 느낌..?*/
  background-color: black;
  width: 100%;
  color:white;
  padding-left: 20px;
}


.modal{
  margin-top: 20px;
  padding : 20px;
  background-color: #eee;
  text-align: left;
}






/*---------------여기까지-------------*/

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

